import React from 'react'
import {withStyles } from '@material-ui/core/styles'
import PreviewCard from '../components/previewCard'
import Masonry from 'react-masonry-component'

/**
 * Card Grid List
 *
 * Display brief information of each section
 *
 * @param data - list of data you wish to print out
 */

const styles = theme => ({
	root:{
	},
	gridContainer:{
	},
	card:{
		
		padding: theme.spacing.unit*2,
		[theme.breakpoints.only("xs")]:{
			width:"100%",
			padding: theme.spacing.unit * .5,
			paddingBottom: theme.spacing.unit *2,
		},
		[theme.breakpoints.only("sm")]:{
			width:"50%",
		},
		[theme.breakpoints.up("md")]:{
			width:"33%",
		},
	}
})

class CardGridList extends React.Component {

	static defaultProps = {
		data:[],
	}

	render() {
		
		const { classes, data } = this.props;


		return (
			<div className={classes.root}>				
					<Masonry className={classes.gridContainer}>
					{
						data.map(work => (
						<div key={work.title} className={classes.card} >
							<PreviewCard data={work}/>
						</div>
					))}
					</Masonry>
				{/* </Grid> */}
			</div>
		)
	}
}

export default withStyles(styles)(CardGridList);



